.badge-check {
  background-image: linear-gradient(to right, #141e30, #243b55);
  height: 350px;
  color: white;
}

.badge-button {
  width: 100px;
  height: 30px;
  background-color: rgb(130, 60, 196);
  border-radius: 2px;
  color: white;
  border: 2px solid white;
  font-size: 15px;
  cursor: pointer;
}

.badge-button:hover {
  background-color: #3c1e59;
}

.badge-check-text {
  padding-top: 15px;
  padding-left: 25px;
  display: flex;
  justify-content: center;
  gap: 2rem;
  font-weight: bold;
}

.tc-text {
  text-align: center;
}

.badge-display {
  margin-left: 400px;
}

.badge {
  background: -webkit-linear-gradient(#ee08c4, #a0c810);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: 800;
  font-size: 20px;
  margin-left: 40px;
}

@media screen and (max-width: 768px) {
  .profile-bio {
    font-size: 25px;
  }

  .badge-check {
    height: 525px;
  }

  .badge {
    font-size: 35px;
  }

  .badge-button {
    width: 150px;
    height: 40px;
    font-size: 20px;
  }
}

@media screen and (max-width: 580px) {
  .profile-bio {
    font-size: 35px;
  }

  .badge-check {
    height: 650px;
    width: 115%;
  }

  .badge {
    font-size: 35px;
  }

  .badge-button {
    width: 180px;
    height: 50px;
    font-size: 25px;
  }
}
