.home-container-1 {
  min-height: 100vh;
  max-width: 1250px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0% auto;
}

.home-container-2 {
  max-width: 1000px;
  width: calc(100% - 164px);
  border: 1px solid #d6d9dc;
  padding: 24px;
  box-sizing: border-box;
}

@media screen and (max-width: 1280px) {
  .App {
    width: 1200px;
    height: 1070px;
  }
}

@media screen and (max-width: 768px) {
  .home-container-2 {
    border: none;
    max-width: 90%;
  }

  .home-container-1 {
    height: 100%;
  }
}

@media screen and (max-width: 580px) {
  .home-container-1 {
    height: 100%;
  }
}
