.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 10;
  background: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  top: 30vh;
  left: 10%;
  width: 80%;
  z-index: 100;
  overflow: hidden;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 10px;
}

.header {
  background: #4f005f;
  padding: 1rem;
}

.header h2 {
  margin: 0;
  color: white;
}

.content {
  padding: 1rem;
  font-size: 25px;
  font-weight: 700;
  color: teal;
}

.badge-img {
  width: 100px;
  height: 100px;
  margin-left: 250px;
}

.actions {
  padding: 1rem;
  display: flex;
  justify-content: flex-end;
}

.actions-btn {
  font: inherit;
  border: 1px solid #4f005f;
  background: #4f005f;
  color: white;
  padding: 0.25rem 1rem;
  cursor: pointer;
}

.actions-btn:hover,
.actions-btn:active {
  background: #741188;
  border-color: #741188;
}

.actions-btn:focus {
  outline: none;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}
