.TC-details-container {
  margin-top: 100px;
  text-align: center;
}

.badge-info {
  color: grey;
  margin-top: 50px;
}

.warning-text {
  padding-top: 150px;
  font-weight: 500;
  font-size: large;
  color: red;
}

@media screen and (max-width: 768px) {
  .TC-details-container {
    margin-top: 120px;
    font-size: 25px;
  }

  .warning-text {
    font-size: larger;
  }
}

@media screen and (max-width: 580px) {
  .TC-details-container {
    margin-top: 160px;
    font-size: 50px;
    margin-left: 20px;
    margin-right: 20px;
  }
}
