.main-nav {
  min-height: 50px;
  width: 100%;
  margin: 0% auto;
  border-top: 3px solid #ef8236;
  box-shadow: 0px 1px 5px #00000033;
  position: fixed;
  z-index: 5;
  top: 0%;
  left: 0%;
  background-color: #f8f9f9;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar {
  height: 100%;
  min-width: 85%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.nav-logo {
  padding: 5px 25px;
}

.nav-item {
  margin: 0px 3px;
  font-size: small;
  font-weight: 500;
  text-decoration: none;
  color: rgb(69, 69, 69);
  transition: 0.2s;
}

.nav-btn {
  cursor: pointer;
  border-radius: 20px;
  padding: 10px 20px;
}

.nav-item:hover {
  background-color: rgb(226, 226, 226);
}

.navbar form {
  flex-grow: 1;
  padding: 0px 12px;
  position: relative;
}

.navbar form input {
  min-width: 50%;
  margin: 0;
  padding: 8px 10px 8px 32px;
  font-size: 13px;
  border: 1px solid #0000003e;
  border-radius: 3px;
}

.navbar form .search-icon {
  position: absolute;
  left: 25px;
  top: 8px;
}

.nav-links {
  padding: 7px 13px;
  border: 1px solid blue;
  border-radius: 3px;
  background-color: #e7f8fe;
  cursor: pointer;
}

.nav-links:hover {
  background-color: #d3e4eb;
}

.translate-page {
  display: flex;
}

#choose-lang {
  margin-top: 22px;
  font-size: 13px;
  font-weight: bold;
}

#google_translate_element {
  margin: 10px;
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .showNav-button {
    display: block;
    border: 0;
    height: 50px;
    width: 60px;
    padding: 0.5rem;
    border-radius: 505;
    background-color: #3f2fb8;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    position: relative;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    margin-top: 50px;
  }

  .main-nav {
    height: 100px;
    font-size: 25px;
  }

  .navbar form .search-icon {
    position: absolute;
    left: 25px;
    top: 15px;
  }

  .nav-btn {
    display: none;
  }

  .navigation-menu ul {
    display: none;
    position: absolute;
    top: 65px;
    left: 60px;
    flex-direction: column;
    width: 50%;
    height: 250px;
    background-color: rgb(231, 238, 248);
    border: 1px solid black;
    font-size: 25px;
    border-radius: 25px;
  }

  .navigation-menu .navigation-links {
    font-weight: 600;
    text-decoration: none;
    text-align: center;
    margin: 0;
  }

  .navigation-menu .navigation-links p {
    color: rgb(39, 47, 154);
    width: 100%;
    padding: 1.5rem, 0;
  }

  .navigation-menu .navigation-links:hover {
    background-color: #eee;
  }

  .navigation-menu.expanded ul {
    display: block;
  }

  .nav-logo img {
    width: 170px;
    height: 50px;
  }

  #choose-lang {
    font-size: 15px;
  }

  .nav-links {
    width: 100px;
    height: 50px;
    text-align: center;
    font-size: 20px;
    margin-left: 20px;
  }
}

@media screen and (min-width: 768px) {
  .navigation-menu {
    display: none;
  }

  .showNav-button {
    display: none;
  }
}

@media screen and (max-width: 580px) {
  .main-nav {
    height: 150px;
  }

  .navbar {
    justify-content: center;
  }

  .nav-logo img {
    width: 230px;
    height: 75px;
  }

  #choose-lang {
    font-size: 20px;
    margin-top: 18px;
  }

  .nav-links {
    width: 120px;
    height: 70px;
    text-align: center;
    font-size: 25px;
  }

  .showNav-button {
    height: 75px;
    width: 80px;
    top: 15px;
  }

  .navigation-menu ul {
    top: 75px;
    left: 35px;
    height: 500px;
    font-size: 50px;
  }

  .fa-2x {
    height: 50px;
    width: 50px;
  }
}
