.main-bar {
  width: calc(100% - 300px - 24px);
  float: left;
  margin: 25px 0px;
  padding: 0;
}

.main-bar-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-bar-header h1 {
  font-weight: 400;
}

.main-bar-header .ask-btn {
  padding: 10px 15px;
  border-radius: 4px;
  background-color: #009bff;
  color: white;
  border: none;
  text-decoration: none;
  transition: 0.3s;
}

.main-bar-header .ask-btn:hover {
  background-color: #0086d8;
}

.display-question-container {
  min-height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: #fdf7e2;
  border-bottom: 1px solid #edeff0;
}

.display-question-container .display-votes-ans {
  padding: 20px;
}

.display-question-container .display-votes-ans p {
  margin: 0%;
  text-align: center;
}

.display-question-details {
  flex-grow: 1;
  padding: 0px 20px;
}

.question-title-link {
  text-decoration: none;
  color: #037ecb;
  transition: 0.3s;
}

.question-title-link:hover {
  color: #009bff;
}

.display-question-details p {
  padding: 4px;
  margin: 0%;
}

.display-tags-time .display-tags {
  display: flex;
  flex-wrap: wrap;
}

.display-tags-time {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.display-tags-time .display-tags p {
  margin: 2px;
  padding: 4px;
  font-size: 13px;
  background-color: #edeff0;
  color: #39739d;
}

.display-tags-time .display-time {
  font-size: 13px;
}

.badge-offer {
  background: linear-gradient(to right, #870000, #190a05);
  height: 75px;
  padding-bottom: 25px;
}

.badge-offer-text {
  padding: 8px;
  color: rgb(61, 246, 70);
}

.badgeAuth-button {
  width: 100px;
  height: 30px;
  background-color: rgb(60, 51, 51);
  border-radius: 2px;
  color: white;
  border: 2px solid white;
  margin-left: 250px;
  font-size: 15px;
  cursor: pointer;
}

.badgeAuth-button:hover {
  background-color: darkslategray;
}

@media screen and (max-width: 768px) {
  .main-bar {
    width: 115%;
    float: none;
    margin-left: 20px;
    margin-right: 25px;
    font-size: 25px;
    margin-top: 50px;
  }

  .badge-offer {
    height: 120px;
    text-align: center;
  }

  .badgeAuth-button {
    height: 50px;
    width: 150px;
    margin-right: 200px;
    font-size: 25px;
  }

  .ask-btn {
    width: 150px;
    height: 50px;
    font-size: 15px;
  }

  .display-tags-time .display-tags p {
    font-size: 17px;
  }

  .display-tags-time .display-time {
    font-size: 17px;
  }
}

@media screen and (max-width: 580px) {
  .main-bar {
    width: 115%;
    float: none;
    margin-left: 25px;
    margin-right: 25px;
    font-size: 35px;
    margin-bottom: 50px;
    margin-top: 100px;
  }

  .badge-offer {
    height: 220px;
    text-align: center;
  }

  .badge-offer-text {
    padding-left: 25px;
    padding-right: 25px;
    line-height: 50px;
  }

  .badgeAuth-button {
    height: 75px;
    width: 250px;
    margin-right: 230px;
    font-size: 35px;
  }

  .ask-btn {
    width: 200px;
    height: 75px;
    font-size: 20px;
  }

  .display-tags-time .display-tags p {
    font-size: 20px;
  }

  .display-tags-time .display-time {
    font-size: 20px;
  }
}
