.right-sidebar {
  float: right;
  width: 300px;
  margin: 40px 0px 15px 24px;
  font-size: 15px;
}

.widget {
  margin-top: 10px;
  box-shadow: 3px 3px 10px rgb(0 0 0 / 5%), -3px -3px 10px rgb(0 0 0 / 5%);
}

.widget h4 {
  background-color: #fbf3d5;
  margin: 0%;
  padding: 15px;
  border: 1px solid #f1e5bc;
}

.right-sidebar-div-1 {
  background-color: #fdf7e2;
  padding: 15px;
  border: 1px solid #f1e5bc;
}

.right-sidebar-div-1 .right-sidebar-div-2 {
  display: flex;
  align-items: flex-start;
  justify-content: space-evenly;
}

.right-sidebar-div-1 .right-sidebar-div-2 p {
  padding-left: 10px;
  margin-top: 0%;
  font-size: 14px;
}

.widget-tags {
  margin-top: 40px;
  box-shadow: 3px 3px 10px rgb(0 0 0 / 5%), -3px -3px 10px rgb(0 0 0 / 5%);
}

.widget-tags h4 {
  margin: 0%;
  padding: 15px;
  background-color: #f8f9f9;
  border: 1px solid #e3e6e8;
}

.widget-tags-div {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-evenly;
  border: 1px solid #e3e6e8;
  padding: 10px;
}

.widget-tags-div p {
  padding: 5px;
  background-color: #e1ecf4;
  color: #39739d;
  border-radius: 2px;
  font-size: 14px;
}

@media screen and (max-width: 768px) {
  .right-sidebar {
    position: relative;
    float: none;
    width: 75%;
    margin-left: 225px;
    font-size: 25px;
  }

  .right-sidebar-div-1 .right-sidebar-div-2 {
    justify-content: center;
    display: inline-flex;
  }

  .right-sidebar-div-1 .right-sidebar-div-2 p {
    font-size: 18px;
  }

  .widget-tags-div p {
    font-size: 20px;
    margin: 10px;
  }

  .right-sidebar-2 {
    display: none;
  }
}

@media screen and (max-width: 580px) {
  .right-sidebar {
    font-size: 50px;
  }

  .right-sidebar-div-1 .right-sidebar-div-2 p {
    font-size: 35px;
  }

  .right-sidebar-div-1 .right-sidebar-div-2 img {
    margin-top: 10px;
    width: 35px;
  }
}
