.tags-h1 {
  margin-top: 50px;
  font-weight: 400;
}

.tags-p {
  margin: 0px;
  font-size: 15px;
}

.tags-list-container {
  padding: 30px 0px;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 10px;
}

.tag {
  padding: 10px;
  border: 1px solid #d2d2d2;
  border-radius: 2px;
}

.tag h5 {
  display: inline-block;
  margin: 10px 0px;
  padding: 5px 5px;
  background-color: #e1ecf4;
  color: #39739d;
}

.tag p {
  font-size: 14px;
  color: #323232;
  line-height: 17px;
}

@media screen and (max-width: 768px) {
  .tags-h1 {
    margin-top: 80px;
  }

  .tags-p {
    font-size: 20px;
  }

  .tags-list-container {
    width: 120%;
  }

  .tag h5 {
    font-size: 20px;
  }

  .tag p {
    font-size: 17px;
    line-height: 20px;
  }
}

@media screen and (max-width: 580px) {
  .tags-h1 {
    margin-top: 130px;
    font-size: 50px;
  }

  .tags-p {
    font-size: 30px;
  }

  .tags-list-container {
    width: 120%;
  }

  .tag h5 {
    font-size: 30px;
  }

  .tag p {
    font-size: 20px;
    line-height: 20px;
  }
}
